import {useState} from 'react';
import './styles.css'

function App() {

  console.log('pooop', window.jsonData);
  
  const [narrowWindow, setNarrowWindow] = useState(false);
  
  function windowResizeHandler(){
  	const w = window.innerWidth;
  	const h = window.innerHeight;
  	if( w/h < 1 ){
  		setNarrowWindow(true);
  	}
  	else{
  		setNarrowWindow(false);
  	}
  }
  window.addEventListener('resize', windowResizeHandler);
  
  function Nav(props){
	if(!narrowWindow){
		return(
			<div className='nav'>
				<button>
					Home
				</button>
				<button>
					New
				</button>
				<div>
					<button>
						S
					</button>
					<input type="text"/>
				</div>
			</div>
		);
	}
	else {
		return (
			<div className='nav'>
				<button>
					Home
				</button>
				<button>
					New
				</button>
			</div>
		);
	}
	
  }
  
  return (
    <div>
	  <Nav/>
    </div>
  );
}

export default App;
